<template>
    <!--    <div class="view-wrapper">-->
    <div class="view-wrapper" :style="{height:pageMixin_browser.clientHeight}">
        <DP_Popup
                :display.sync="popup.show"
                :show-close="true"
                background-color="white"
                :title="popup.title"
        >
            <img class="qrcode" :src="pageQrcode_img" alt="">
            <div>请打开微信扫一扫</div>
        </DP_Popup>
        <div class="content">
            <!--            <img ref="qrcode" width="150px" v-show="qrcode" crossorigin="anonymous"-->
            <!--                 :src="qrcode"-->
            <!--            />-->
            <img v-if="userInfo.avatar" ref="avatar" class="avatar" width="100px" crossorigin="anonymous"
                 :src="userInfo.avatar"
                 alt="">
            <template v-show="!pageParameter.isInit &&  showImg">
                <div class="poster-wrapper" ref="posterWrapper">
                    <img ref="poster_output" class="poster_output" v-show="output_img" :src="output_img" alt="">
                    <img ref="poster" v-show="!output_img" class="origin"
                         crossorigin="anonymous"
                         :src="pageParameter.posterAndShareContent.length>0 ? pageParameter.posterAndShareContent[0].url :''"
                         alt="">
                    <canvas id="canvas" ref="canvas"></canvas>
                    <!--                <canvas id="canvas" ref="canvas" :width="canvas_width" :height="canvas_height"></canvas>-->
                </div>
            </template>

            <div class="in-development" v-if="output_img===''">{{tips}}</div>

        </div>

        <div class="share-in-app" v-if="pageMixin_browser.device.isDPApp && output_img">
            <div class="option-wrapper">
                <a class="option" :href="'uniwebview://wechatsharetofriend?image='+output_img">
                    <div class="icon friend"></div>
                    <div class="label">微信</div>
                </a>
                <a class="option" :href="'uniwebview://wechatsharetomoments?image='+output_img">
                    <div class="icon moment"></div>
                    <div class="label">朋友圈</div>
                </a>
                <a class="option">
                    <div class="icon scan" @click="openPopup"></div>
                    <div class="label">扫码</div>
                </a>
            </div>

        </div>
        <div class="share-in-wechat" v-if="pageMixin_browser.device.isWechat && output_img">
            <div class="arrow"></div>
            <div class="tips">长按上图保存到相册，分享给好友</div>

        </div>

    </div>
</template>

<script>
    import pageMixin from "@/mixin/pageMixin";
    import {getPageUrlQrCode, getQrCode} from "@/api/parameter";
    import {mapGetters} from "vuex";
    // import {getLocalData, setLocalData} from "@/utils/function";
    import DP_Popup from '../../components/DP_Popup'

    export default {
        metaInfo() {
            return {
                title: this.$route.meta.title || '熊猫博士', // set a title
                meta: [{                 // set meta
                    name: 'keywords',
                    content: this.$route.meta.title + ",熊猫博士"
                }],
            }
        },
        name: "index",
        components: {DP_Popup},
        mixins: [pageMixin],
        props: {},
        data() {
            return {
                tips: "海报制作中，请稍后...",
                posterData: [],
                showImg: false,
                output_img: '',
                p_img: false,
                a_img: false,
                qrcode: '',
                pageQrcode_img: "",
                popup: {
                    show: false,
                    title: "",
                }
            }
        },
        watch: {},
        computed: {
            ...mapGetters({
                pageParameter: "getPageParameter",
                userInfo: "getUserInfo",
            }),
        },
        methods: {
            openPopup() {

                this.popup.show = true;

            },
            async draw() {
                const dpr = window.devicePixelRatio;
                console.log('window.devicePixelRatio', dpr)
                let user_id = this.$route.query.user_id ? this.$route.query.user_id : this.userInfo.id;
                if (!user_id) {
                    this.tips = "用户信息错误";
                    this.showImg = false;
                    return;
                }

                const res = await getQrCode(user_id);
                const {data: {data, code}} = res;
                const qrcode = new Image();
                if (code === 0) {
                    qrcode.src = 'data:image/jpeg;base64,' + data;
                }

                qrcode.onload = async () => {


                    const poster = await this.getImg('poster');
                    const {canvas} = this.$refs;
                    const width = 640;
                    const height = 1138;
                    canvas.width = width;
                    canvas.height = height;
                    const ctx = canvas.getContext('2d');
// eslint-disable-next-line no-debugger
                    if (this.$store.getters.getBrowser.device.isWechat && this.userInfo.avatar) {

                        const avatar = await this.getImg('avatar');
                        ctx.drawImage(poster, 0, 0, width, height);
                        ctx.fillStyle = "white"
                        ctx.font = "22px Helvetica, Arial, sans-serif";
                        ctx.fillText(this.userInfo.name, 130, 65);
                        ctx.font = "18px Helvetica, Arial, sans-serif";
                        ctx.fillText(this.pageParameter.posterAndShareContent[0].shareContent, 130, 95);
                        // ctx.drawImage(avatar, 8 * dpr, 8 * dpr, 28 * dpr, 30 * dpr);
                        ctx.drawImage(avatar, 30, 30, 80, 80);
                        // ctx.drawImage(qrcode, 255 * dpr, 550 * dpr, 70 * dpr, 70 * dpr);
                        ctx.drawImage(qrcode, 430, 936, 150, 150);
                        this.output_img = canvas.toDataURL();


                    } else {

                        ctx.drawImage(poster, 0, 0, width, height);
                        // eslint-disable-next-line no-debugger
                        ctx.fillStyle = "white"
                        ctx.font = "22px Helvetica, Arial, sans-serif";
                        ctx.fillText(this.pageParameter.posterAndShareContent[0].shareContent, 30, 65);
                        // ctx.drawImage(qrcode, 100 * dpr, 400 * dpr, 70 * dpr, 70 * dpr);
                        ctx.drawImage(qrcode, 430, 936, 150, 150);
                        this.output_img = canvas.toDataURL();


                    }


                }


            },
            getPageQrCode() {

                getPageUrlQrCode("com.drpanda.chineseacademy", true).then((res) => {
                    const {data: {data, code}} = res;
                    if (code === 0) {
                        this.pageQrcode_img = 'data:image/jpeg;base64,' + data;
                    }
                })
            },
            getImg(ref) {
                const img = this.$refs[ref];
                return new Promise((resolve, reject) => {
                    if (img.complete) {
                        resolve(img);
                        console.log('图片加载已完成');
                    } else {
                        img.onload = () => {
                            resolve(img);
                        }
                        img.onerror = () => {
                            this.dp_Tips('提示', '海报加载错误 请重试')
                            reject('图片加载失败');
                        }
                        img.onabort = () => {
                            this.dp_Tips('提示', '海报加载失败 请重试')
                            reject('图片取消加载');
                        }


                    }

                })


            },
            async getData() {
                const b = await this.$store.dispatch("getPageParameter", {disableLoading: true});
                if (b) {
                    this.posterData = this.pageParameter.posterAndShareContent;
                    setTimeout(() => {
                        this.draw();
                    }, 500);
                }
            },
        },
        created() {

            this.getPageQrCode();
            setTimeout(() => {
                const wx = this.pageMixin_jsSdk;
                if (wx) {
                    wx.hideMenuItems({
                        menuList: [
                            'menuItem:share:appMessage',
                            'menuItem:share:timeline',
                            'menuItem:share:qq',
                            'menuItem:share:weiboApp',
                            'menuItem:share:favorite',
                            'menuItem:share:facebook',
                            'menuItem:share:QZone',
                            "menuItem:favorite",
                            'menuItem:copyUrl',
                            "menuItem:openWithQQBrowser",
                            "menuItem:openWithSafari",
                        ] // 要隐藏的菜单项，只能隐藏“传播类”和“保护类”按钮，所有menu项见附录3
                    });

                }
            }, 1500);
            //暂时先用定时器
        }
        ,
        mounted() {
            this.pageMixin_setRequestCompleted(true);
            if (this.pageParameter.isInit) {
                this.getData();
            } else {
                setTimeout(() => {
                    this.draw();
                }, 500);
            }
            setTimeout(() => {
            }, 500);

        }

    }
</script>

<style lang='scss' scoped>
    @import "../../assets/global/style/mixin";

    .view-wrapper {
        background-color: #333333;
        /*background-image: url("../../assets/h5/poster/bgw.png");*/
        background-repeat: no-repeat;
        /*animation: bg 30s linear infinite alternate;*/
        /*transform: translateZ(0);*/
        /*background-size: cover;*/
        position: relative;
        padding-bottom: 0;
        overflow: hidden;
        @include animation_bg("../../assets/global/bg.png", .2);

        .qrcode {
            width: 100%;
        }

        .content {
            height: calc(100% - 300px);
            position: relative;

            .in-development {
                font-size: 56px;
                color: #a7a7a7;
                position: absolute;
                top: 50%;
                transform: rotateY(-50%);
                margin-top: -300px;
                width: 100%;
                text-align: center;
            }

            .avatar {
                position: absolute;
                width: 0;
                right: 0;
                z-index: 1;

            }

            #canvas {
                display: none;
                width: 750px;
                height: 1334px;
                margin: 0 auto;
                position: absolute;
                top: 0;
                left: 50%;
                margin-left: -375px;

            }

            .poster-wrapper {
                position: absolute;
                text-align: center;
                top: 50%;
                left: 50%;
                width: 750px;
                transform: translate(-50%, -50%);
                overflow: hidden;
                z-index: 1;
                position: relative;

                .origin {
                    margin-left: 99999999px;
                    opacity: 0;
                    width: 750px;
                    height: 1334px;
                }

                img {
                    width: 70%;
                    animation: zoomIn .6s linear;
                    /*width: 750px;*/
                    /*height: 1334px;*/

                }
            }
        }


        .share-in-app {
            width: 100%;
            height: 260px;
            background-color: #ffffff;
            position: fixed;
            bottom: 0;
            left: 0;
            z-index: 1;

            .option-wrapper {
                display: flex;
                width: 750px;
                margin: 0 auto;
                height: 100%;
                align-items: center;


                .option {
                    font-size: 33px;
                    flex: 1;
                    display: block;

                    .label {
                        color: gray;
                        text-align: center;
                    }

                    .icon {
                        width: 100%;
                        height: 150px;
                        background-size: contain;
                        background-repeat: no-repeat;
                        background-position: center center;
                        margin-bottom: 15px;

                    }

                    .friend {
                        background-image: url("../../assets/h5/poster/Group5.png");
                    }

                    .moment {
                        background-image: url("../../assets/h5/poster/Group4.png");
                    }

                    .scan {
                        background-image: url("../../assets/h5/poster/Group9.png");
                    }
                }

            }

        }

        .share-in-wechat {
            width: 100%;
            height: 260px;
            background-color: transparent;
            position: fixed;
            bottom: 0;
            left: 0;
            color: white;
            text-align: center;
            animation: fadeIn 3s linear;
            z-index: 1;

            .arrow {
                background-color: #ffcd00;
                width: 120px;
                height: 120px;
                background-image: url("../../assets/ca/poster/arraw_up.png");
                background-repeat: no-repeat;
                background-size: 100% 100%;
                border-radius: 50%;
                margin: 0 auto;
                margin-bottom: 50px;
            }

            .tips {
                font-size: 40px;
            }
        }
    }
</style>
